.App {
  background-color: rgb(239, 184, 134);
  text-align: center;
  align-content: center;
  width: 55%;
  margin: 0 auto;
  color: #1e1448;
}
.serialList {
  width: 100%;
  height: 100%;
}
.content {
  max-width: 95%;
  margin: 0 auto;
}
@media (max-width: 1399px) {
  .App {
    width: 765px;
  }
}

@media (max-width: 768px) {
  .App {
    width: 100%;
  }
}
